<template>
	<div v-bind="$attrs"></div>
</template>

<script>
import EasterEggTriggerCore from '../plugin/index';

export default {
	name: 'EasterEggComponent',
	props: {
		callback: {
			default: null,
			type: Function,
		},
		pattern: {
			default: () => ['ArrowUp', 'ArrowUp', 'ArrowDown', 'ArrowDown', 'ArrowLeft', 'ArrowRight', 'ArrowLeft', 'ArrowRight', 'b', 'a'],
			type: Array,
		},
		target: {
			default: 'body',
			type: String,
		},
		triggered: {
			default: null,
			type: Function,
		},
		type: {
			default: 'keydown',
			type: String,
		},
	},
	data: () => ({
		defaultOptions: {
			delay: 500,
		},
	}),
	computed: {},
	watch: {},
	created() {
		console.log('EasterEggComponent created');
	},
	mounted() {
		EasterEggTriggerCore.init(this.defaultOptions, this.$attrs);
	},
	methods: {
		buildOptions() {
			const options = {
				callback: this.callback,
				pattern: this.pattern,
				target: this.target,
				triggered: this.triggered,
				type: this.type,

				destroyBus: false,
				name: 'easter-egg',

				withBus: true,
			};

			return options;
		},
	},
};
</script>
